import React, {useState} from 'react'
import './DashboardPage.css';
import TitleMenuButton from '../../components/TitleMenuButton/TitleMenuButton';
import General from './General';
import LinkSocial from './LinkSocial';
import ContentPage from './ContentPage';

function DashboardPage(props) {

    const [activeButton, setActiveButton] = useState("Gnral");

    const handleButtonClick = (title) => {
        setActiveButton(title);
        console.log(activeButton);
    };
 
    return (
        <div className='DashboardPage'>
            <div className='menuLeft'>
                <TitleMenuButton title={"Gnral"} active={activeButton === "Gnral" ? true : false} onClick={() => handleButtonClick("Gnral")} color={props.color} icon={"fa-solid fa-gear"} />
                <TitleMenuButton title={"Liens rseaux sociaux"} active={activeButton === "LinkSocial" ? true : false} onClick={() => handleButtonClick("LinkSocial")} color={props.color} icon={"fa-solid fa-link"} />
                <TitleMenuButton title={"Contenu de votre page"} active={activeButton === "ContentPage" ? true : false} onClick={() => handleButtonClick("ContentPage")} color={props.color} icon={"fa-solid fa-table-columns"} />
            </div>
            <div className='settings'>
                {activeButton === "Gnral" ? (<General color={props.color} namespace={"Maroon 5"} />):(null)}
                {activeButton === "LinkSocial" ? (<LinkSocial />):(null)}
                {activeButton === "ContentPage" ? (<ContentPage color={props.color}/>):(null)}
            </div>
        </div>
    );
}

export default DashboardPage;